<template>
	<view class="message-list" @click="handleClick">
		<view class="dian-title">
			<!-- <view class="dian"></view> -->
			<view class="title">{{order.Titie}}</view>
		</view>
		<view class="content">{{order.Content}}</view>
		<view class="time">{{order.CreateTime}}</view>
	</view>
</template>

<script>
	export default {
		props: {
			order: {
				default: {}
			}
		},
		methods: {
			handleClick() {
				let items = JSON.stringify(this.order);
				uni.setStorageSync("messageList", items);
				uni.navigateTo({
					url: '/pages/user-center/user-message/message-information/index'
				})
			}
		}
	}
</script>

<style scoped lang="scss">
	.message-list {
		background: #fff;
		margin-top: 30rpx;
		padding: 30rpx;
		border-radius: 16px;
	}

	.dian-title {
		display: flex;
		justify-content: start;
		align-items: center;

		.dian {
			width: 12rpx;
			height: 12rpx;
			background: #FEB042;
			border-radius: 50%;
		}

		.title {
			padding-left: 10rpx;
			font-size: 32rpx;
			font-weight: 500;
			color: #333333;
			line-height: 44rpx;
		}
	}

	.content {
		font-size: 30rpx;
		font-weight: 400;
		color: #666666;
		line-height: 48rpx;
		letter-spacing: 1rpx;
		padding: 20rpx 0;
	}

	.time {
		font-size: 24rpx;
		font-weight: 400;
		color: #999999;
		line-height: 34rpx;
	}
</style>
